.button {
  position: relative;
  z-index: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 20px;

  font-family: $ff-Roboto-Condensed;
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  background: transparent;
  color: $color-default-white;
  border: 4px solid $color-default-white;

  cursor: pointer;

  @include hover-focus {
    color: $color-default-white;
    background: linear-gradient(97.05deg, $color-torch-red 17.6%, $color-cobalt 95.31%);
    padding: 24px 20px;
    border: none;
  }

  &--secondary {
    color: $color-sapphire;
    border: 3px solid $color-sapphire;
    padding: 22px 0 23px 0;

    @include hover-focus {
      padding: 25px;
    }
  }

  &:active {
    opacity: 0.5;
  }
}
